export default {
	name: 'numSelect',
	props: {
		value: { //传入值
			type: Number
		},
		name: { //name 同原生 用于表单提交
			type: String
		},
		min: { //最小值 不做限制则传入null 默认为0
			type: Number,
			default: 0
		},
		max: { //最大值 不做限制则传入null
			type: Number,
			default: 100
		},
		disable: { //是否禁用
			type: Boolean,
			default: false
		},
		color: { //风格颜色 输入颜色代码即可
			type: String,
			default: '#ea5a59'
		},
		model: { //风格类型 默认为1 一共5种
			type: String,
			default: '1'
		},
		input: { //允许输入
			type: Boolean,
			default: false
		},
		step: { //步长
			type: Number,
			default: 1
		},
		size: { //大小
			type: String,
			default: '40'
		}
	},
	data() {
		return {
			num: 0,
			closeMinus: false,
			closeAdd: false,
			maxlength: null,
			isDigit: false
		}
	},
	created() {
		if (this.value || this.value == 0) {
			this.num = this.value;
		}
		this.decideMinus();
		this.decideAdd();
		let len = null;
		if (this.max == null) {
			len = 100000000
		} else {
			len = (this.max + "").length
		}
		if (typeof this.step !== 'Number' && this.step % 1 !== 0) {
			len += 3;
			this.isDigit = true;
		}
		this.maxlength = len;
	},
	watch: {
		value(n, o) {
			this.num = n;
		}
	},
	methods: {
		minus() {
			if (!this.disable) {
				if (this.min || this.min == 0) {
					if (this.min === this.num) {
						this.num = this.min
						this.closeMinus = true
					}else{
						let v = this.num -= this.step;
						let d = this.isDigit ? Number((v).toFixed(2)) : v
						this.num = d,
						this.closeAdd = false
						this.output();
					}
				}
			}
			
		},
		add() {
			if (!this.disable) {
				if (this.max || this.max == 0) {
					if (this.num === this.max) {
						return false;
					}
				}
				let v = this.num += this.step;
				let d = this.isDigit ? Number(v.toFixed(2)) : v
				this.num = d;
				this.closeMinus = false;
				if (this.max || this.max == 0) {
					if (this.num === this.max) {
						this.closeAdd = true
					}
				}
			}
			this.output();
		},

		decideMinus() {
			if (this.num == this.min > 1) {
				this.closeMinus = true
			} else {
				this.closeMinus = false
			}
		},
		decideAdd() {
			if (this.max || this.max == 0) {
				if (this.num == this.max) {
					this.closeAdd = true
					return
				}
			}
			this.closeAdd = false
		},

		numInput(e) {
			let v = e.detail.value;
			if (typeof v !== 'Number' && v % 1 !== 0) {
				this.isDigit = true;
				this.num = (v == "" ? 0 : Number(Number(v).toFixed(2)));
			} else {
				this.num = Number(v == "" ? 0 : v);
			}
			this.output();
		},

		blur(e) {
			if (e.detail.value == "") {
				this.num = 0
			};
			if (this.max || this.max == 0) {
				if (this.num === this.max) {
					this.closeAdd = true
				}
			}

			if (this.min || this.min > 0) {
				if (this.num === this.min) {
					this.closeMinus = true
				}
			}
		},

		output() {
			this.$emit('getValue', this.num);
		}
	}
}
